* {
  box-sizing: border-box;
};
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  
};
#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
};
a {
  text-decoration: none;
};
img {
  display: block;
  width: 100%;
  height: 100%;
};
p {
  padding: 0;
  margin: 0;
};
b {
  font-size: 12px;
  text-align: center;
};
.hidden {
  display: none;
}
.gallery{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-top: 10px;
  margin-right: -10px;
  margin-bottom: -10px;

}
.photo-card {
  width: 260px;
  margin-right: 10px;
  margin-bottom: 10px;
  color: #7c7c7c;
  background: #f1f1f1;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: inset 4px -6px 6px -6px rgb(150 79 23 / 60%), inset -6px -6px 5px -6px rgb(150 79 23 / 32%);
transition: .7s;
};
.img-wrapper {
  width: 100%;
  height: 180px;
  margin-bottom: 15px;
  overflow: hidden;
};
.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
};
.info-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.search-form {
  padding: 20px 0;
  width: 100%;
  margin-bottom: 25px;
  background: #514eb1;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 6px -6px #777;
}

.search-form input {
  height: 35px;
  border-radius: 5px;
  border: none;
  margin-right: 10px;
  width: 300px;
  padding-left: 5px;
  font-size: 18px;
  outline-color: #514eb182;
}

.search-form button {
  height: 35px;
  border: none;
  background: #ceccff;
  border-radius: 5px;
  cursor: pointer;
}